export default {
	props: {
		item: Object
	},
	data() {
		return {
			isAttention: this.item.isAttention
		}
	},
	methods: {
		handleAddAttention() {
			this.isAttention = true
			uni.showToast({
				title: "关注成功"
			})
		},
		//图片点击事件
		handleImgItemClick(index){
			uni.previewImage({
				urls:this.item.titlePicArr,
				current:index
			})
		}
	},
}
